import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useParams, withRouter } from "react-router-dom";
import Bar from "../../componente/bar/bar";
import Goback from "../../componente/goback/goback";
import Nav from "../../componente/nav/nav";

function HistoryUser() {
  const [orders, setOrders] = useState(false);
  const { id } = useParams();
  useEffect(() => {
    const token = window.localStorage.getItem("token");
    if (id) {
      axios
        .get("/orders/allcanceled", {
          headers: { Authorization: `Bearer ${token}` },
        })
        .then((e) => {
          setOrders(e.data);
        });
    }
  }, [id]);
  return (
    <>
      <Nav />
      <Bar />
      <Goback />
      <div className="base">
        <div className="base-detail">
          <div>
            <table>
              <thead>
                <tr>
                  <th>Estado</th>
                  <th>Metodo de pago</th>
                  <th>Total $</th>
                  <th>Total BS.S</th>
                  <th>Vendedor</th>
                </tr>
              </thead>
              <tbody>
                {orders
                  ? orders.map((e) => {
                      let estado = "Cargando";
                      switch (e.properties.status_code) {
                        case 2724:
                          estado = "Sin atender";
                          break;
                        case 2726:
                          estado = "Atendiendo";
                          break;
                        case 2723:
                          estado = "Por Pagar";
                          break;
                        case 2725:
                          estado = "Anulado Por Cliente";
                          break;
                        case 2727:
                          estado = "Anulado Por Sistema";
                          break;

                        case 2722:
                          estado = "Pagado";
                          break;
                        case 2721:
                          estado = "Despachado";
                          break;
                        case 2720:
                          estado = "Eliminado";
                          break;
                        default:
                          estado = e.properties.status_code;
                          break;
                      }
                      return (
                        <tr key={e.id}>
                          <td>
                            <Link
                              className="btn primary text-white"
                              to={`/orders/${e.id}`}
                            >
                              {estado}
                            </Link>
                          </td>
                          <td>{e.properties.cart_properties.payment_type}</td>
                          <td>
                            ${" "}
                            {Intl.NumberFormat("es-VE").format(
                              e.subtotal.toFixed(2)
                            )}
                          </td>
                          <td>
                            BS.S{" "}
                            {Intl.NumberFormat("es-VE").format(
                              e.total.toFixed(2)
                            )}
                          </td>
                          <td>
                            {e.properties.seller_name} (
                            {e.properties.seller_user})
                          </td>
                        </tr>
                      );
                    })
                  : ""}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
}
export default withRouter(HistoryUser);
